<script setup>
import { routeConfig } from '@/router/index'
import { useRouter, useRoute } from 'vue-router'
import { useI18n } from 'vue-i18n'
import { ref } from 'vue'

import canadaIcon from '@/assets/images/canada.png'
import chinaIcon from '@/assets/images/china.png'

const { locale } = useI18n()
const route = useRoute()
const nav = ref([route.name])
const lan = ref([locale.value])

const setItem = val => localStorage.setItem('language', val)
</script>
<template>
  <v-app-bar flat>
    <!-- <v-app-bar flat style="position: sticky"> -->
    <v-container class="fill-height d-flex align-center">
      <img :width="178" src="@/assets/images/logo-dark.png" alt="" />

      <v-spacer></v-spacer>

      <v-item-group v-model="nav" class="_headerVItemGroup">
        <v-item v-for="item in routeConfig" :key="item.name">
          <template v-slot:default="{ toggle }">
            <v-btn
              variant="text"
              class="text-none"
              :to="`${item.path}`"
              :value="item.name"
            >
              {{ $t(item.meta?.menu) }}
            </v-btn>
          </template>
        </v-item>
      </v-item-group>
      <v-btn>
        <template v-slot:prepend>
          <img
            :width="24"
            :src="lan[0] === 'en' ? canadaIcon : chinaIcon"
            alt=""
          />
        </template>

        {{ lan[0]?.toLocaleUpperCase() }}

        <v-overlay
          activator="parent"
          location-strategy="connected"
          scroll-strategy="close"
          close-on-content-click
          :scrim="false"
        >
          <v-card class="mx-auto" width="100">
            <v-list
              v-model:selected="lan"
              @update:selected="
                val => {
                  $i18n.locale = val[0]
                  setItem(val[0])
                }
              "
              mandatory
            >
              <v-list-item
                color="#295c7a"
                title="English"
                value="en"
              ></v-list-item>
              <v-list-item
                color="#295c7a"
                title=""
                value="cn"
              ></v-list-item>
            </v-list>
          </v-card>
        </v-overlay>
      </v-btn>
    </v-container>
  </v-app-bar>
</template>

<style lang="less">
._headerVItemGroup {
  .v-btn--active {
    .v-btn__overlay {
      background: unset;
    }

    .v-btn__content {
      border-bottom: 2px solid #252b42;
      margin-top: 2px;
    }
  }
}
</style>
